* {
	margin: 0;
	padding: 0;
	border: 0;
	outline: none;
	-webkit-box-sizing: content-box;
	-webkit-font-smoothing: antialiased;
	box-sizing: content-box;
	font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, sans-serif;
}

.tagged_class span {
	background-color: #f1f8ff;
	color: #3072f6;
	padding: 3px;
	border-radius: 3px;
}

.tagged_class span:hover {
	background-color: #dceeff;
}

.tagged_class span:active {
	background-color: #c6e3fd;
}

body {
	-webkit-font-smoothing: antialiased;
	overflow: hidden;
	color: rgba(0, 0, 0, .701961);
	text-rendering: optimizeLegibility;
	-webkit-tap-highlight-color: transparent;
}

.branding,
.branding-dark,
.searchbox,
.searchbox-dark,
.searchbutton,
.searchbutton-dark,
.quarter,
.quarter-dark,
.btn-close,
.btn-close-dark,
.btn-pin,
.btn-pin-dark,
.btn-pin-active,
.btn-pin-active-dark,
.units,
.units-dark,
.card,
.card-dark,
.card-active,
.card-active-dark,
.blankcard,
.blankcard-dark,
.ribbon,
.ribbon-dark,
.card-waitlist,
.card-waitlist-dark,
.card-pin,
.empty-list,
.empty-list-dark,
.list-help div,
.list-help-dark div,
.bling-strip-icon,
.metric-header,
.metric-body,
.schedule-titles-block,
.schedule-titles-block-dark {
	display: flex;
	justify-content: center;
	align-items: center;
}

.branding,
.searchbox {
	background: linear-gradient(90deg, #334bfd, #00c6ff);
}

.branding-dark,
.searchbox-dark {
	background: linear-gradient(90deg, #4c61fa, #ca1cec);
}

.branding,
.branding-dark {
	color: #fff;
	height: 100px;
	font-size: 28px;
}

.branding h1,
.branding-dark h1 {
	font-weight: 400;
}

.branding svg,
.branding-dark svg {
	position: relative;
	margin: 0 10px;
	top: -8px;
}

.searchbox,
.searchbox-dark {
	padding-bottom: 20px;
}

.searchbox input,
.searchbox-dark input {
	outline: none;
	width: 50vw;
	height: 50px;
	border-radius: 7px 0 0 7px;
	text-indent: 20px;
	font-size: 16px;
}

.searchbox-dark input {
	background-color: #333;
	caret-color: #fff;
	color: #fff;
}

.searchbox input::-webkit-input-placeholder,
.searchbox-dark input::-webkit-input-placeholder {
	color: #333;
}

.searchbox-dark input::-webkit-input-placeholder {
	color: #fff;
}

.searchbutton,
.searchbutton-dark {
	cursor: pointer;
	height: 50px;
	width: 50px;
	border-radius: 0 7px 7px 0;
	background-color: #2181f7;
}

.searchbutton-dark {
	background-color: #724ae2;
}

.quarter,
.quarter-dark {
	cursor: pointer;
	width: 20vw;
	height: 50px;
	font-weight: 500;
	font-size: 14px;
	text-indent: 8px;
	color: #333;
	background-color: #f2f2f2;
	-moz-appearance: none;
	-webkit-appearance: none;
	border-radius: 0;
}

.quarter-dark {
	color: #fff;
	background-color: #4e4e4e;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.quarter-loading,
.quarter-dark-loading {
	border: 4px solid #fff;
	border-top: 4px solid #2181f7;
	border-radius: 50%;
	width: 20px;
	height: 20px;
	animation: spin 1s linear infinite;
}

.quarter-dark-loading {
	border: 4px solid #333;
	border-top: 4px solid #8254ff;
}

.list,
.list-dark,
.list-short,
.list-short-dark {
	background-color: #eee;
	height: calc(100vh - 210px);
	padding: 20px 0;
	overflow-y: auto;
}

.list-dark,
.list-short-dark {
	background-color: #111;
}

.list-short,
.list-short-dark {
	height: calc(100vh - 260px);
}

.blankcard-header,
.blankcard-header-dark,
.blankcard-subtitle,
.blankcard-subtitle-dark {
	margin-left: 20px;
	width: 70px;
	height: 18px;
	border-radius: 3px;
	margin-bottom: 7px;
	background-color: #eee;
	animation: pulse 0.5s infinite ease-in-out;
	-webkit-animation: pulse 0.5s infinite ease-in-out;
}

.blankcard-subtitle,
.blankcard-subtitle-dark {
	width: 200px;
	height: 14px;
	margin-bottom: 0;
}

.blankcard-header-dark,
.blankcard-subtitle-dark {
	background-color: #444;
}

@keyframes pulse {
	0% {
		background-color: rgba(165, 165, 165, .2);
	}
	50% {
		background-color: rgba(165, 165, 165, .4);
	}
	100% {
		background-color: rgba(165, 165, 165, .2);
	}
}

@-webkit-keyframes pulse {
	0% {
		background-color: rgba(165, 165, 165, .2);
	}
	50% {
		background-color: rgba(165, 165, 165, .4);
	}
	100% {
		background-color: rgba(165, 165, 165, .2);
	}
}

.card,
.card-dark,
.blankcard,
.blankcard-dark,
.card-active,
.card-active-dark {
	cursor: pointer;
	justify-content: space-between;
	background-color: #fff;
	font-size: 20px;
	height: 85px;
	text-indent: 20px;
	border-radius: 7px;
	max-width: 700px;
	margin: 0 auto;
	color: inherit;
	text-decoration: none;
	box-shadow: 0 0 0 1px transparent;
	position: relative;
}

.card-active,
.card-active-dark {
	box-shadow: 0 0 0 3px #2270ee;
}

.card-active-dark {
	box-shadow: 0 0 0 3px #76d0fc;
}

.card-dark,
.card-active-dark {
	color: #fff;
	background-color: #333;
}

.card::after,
.card-dark::after {
	content: '';
	height: 85px;
	box-shadow: 0 0 0 4px #ccc;
	border-radius: 7px;
	width: 100%;
	opacity: 0;
	position: absolute;
	left: 0;
	top: 0;
	transition: opacity 50ms ease-in-out;
	will-change: opacity;
}

.card-dark::after {
	box-shadow: 0 0 0 4px #555;
}

.card:hover::after,
.card-dark:hover::after {
	opacity: 1;
}

.card-width {
	width: 80%;
}

.card-code {
	font-size: 22px;
	font-weight: 500;
	margin-bottom: 3px;
}

.card-title,
.card-title-dark {
	color: #2270ee;
	font-weight: normal;
	font-size: 16px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	margin-bottom: 0px;
}

.card-title-dark {
	color: #76d0fc;
}

.blankcard-dark {
	background-color: #333;
}

.rightpane,
.rightpane-dark {
	width: 100%;
	background-color: #fff;
}

.rightpane-dark {
	color: #fff;
}

.btn-close,
.btn-close-dark,
.btn-pin,
.btn-pin-dark,
.btn-pin-active,
.btn-pin-active-dark,
.units,
.units-dark {
	width: 50px;
	height: 50px;
	background-color: #fff;
	border-radius: 7px;
	margin-right: 50px;
	cursor: pointer;
	box-shadow: 0 0 0 2px #ddd;
}

.btn-pin,
.btn-pin-dark,
.btn-pin-active,
.btn-pin-active-dark,
.units,
.units-dark {
	margin-right: 10px;
}

.btn-close-dark,
.btn-pin-dark,
.units-dark {
	background-color: #555;
	box-shadow: 0 0 0 2px #777;
}

.btn-close svg path,
.btn-close-dark svg path,
.btn-pin svg path,
.btn-pin-dark svg path {
	fill: #333;
}

.btn-pin-active,
.btn-pin-active-dark {
	background-color: #edb576;
	box-shadow: 0 0 0 2px #edb576;
}

.btn-pin-active {
	background-color: #ffc481;
	box-shadow: 0 0 0 2px #ffc481;
}

.btn-pin-active svg path,
.btn-pin-active-dark svg path,
.btn-close-dark svg path,
.btn-pin-dark svg path {
	fill: #fff;
}

.units,
.units-dark {
	width: auto;
	background-color: #fff;
	padding: 0 30px;
	font-weight: 600;
	flex-direction: column;
	font-size: 26px;
	cursor: pointer;
	color: #333;
}

.units-dark {
	background-color: #555;
	color: #fff;
}

.units span,
.units-dark span {
	font-size: 12px;
	margin-top: 2px;
}

.units:hover,
.btn-pin:hover,
.btn-close:hover {
	background-color: #f0f0f0;
}

.units-dark:hover,
.btn-pin-dark:hover,
.btn-close-dark:hover {
	background-color: #666;
}

.btn-close:active,
.btn-close-dark:active {
	background-color: red;
}

.btn-close svg:active path {
	fill: #fff;
}

.title,
.title-dark {
	margin-left: 50px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.title b,
.title-dark b {
	color: #2270ee;
	font-size: 18px;
}

.title h1,
.title-dark h1 {
	margin-bottom: 2px;
}

.title-dark b {
	color: #76d0fc;
}

.ribbon,
.ribbon-dark {
	top: 0px;
	z-index: 1px;
	height: 100px;
	justify-content: space-between;
	background-color: #f5f5f5;
}

.ribbon-dark {
	background-color: #333;
}

.content,
.content-dark {
	padding: 20px 50px;
	height: calc(100vh - 140px);
	overflow-y: auto;
}

.content-dark {
	background-color: #444;
}

.content p,
.content-dark p {
	margin: 10px 0;
	font-size: 18px;
	max-width: 924px;
	word-wrap: break-word;
	line-height: 1.3;
}

.content p:first-of-type,
.content-dark p:first-of-type {
	margin-top: 0;
}

.content b,
.content-dark b {
	font-size: 18px;
}

.content h1,
.content-dark h1 {
	margin: 25px 0;
}

.emptypane,
.emptypane-dark {
	width: 72vw;
}

.emptypane-dark {
	background-color: #444;
}

.card-waitlist,
.card-waitlist-dark,
.card-pin {
	height: 100%;
	margin-right: 20px;
}

.card-waitlist svg {
	fill: #7f7f7f;
}

.card-waitlist-dark svg {
	fill: #a1a1a1;
}

.card-waitlist-empty-icon {
	width: 21px;
}

.card-pin svg {
	fill: #ffc463;
}

.empty-list,
.empty-list-dark {
	height: 100%;
	color: #333;
	flex-direction: column;
	text-align: center;
}

.empty-list-dark {
	color: #fff;
}

.list-help,
.list-help-dark {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-top: 30px;
}

.list-help div,
.list-help-dark div {
	width: 50px;
	height: 50px;
	background-color: #0059ff;
	border-radius: 5px;
	margin: 0 10px;
}

.list-help-dark div {
	background-color: #4c61fa;
}

.navigation,
.navigation-dark {
	height: 50px;
	background-color: #f8f8f8;
	color: #333;
	border-top: 1px solid #ddd;
	display: flex;
}

.navigation-dark {
	background-color: #333;
	color: #fff;
	border-top: 1px solid #333;
}

.btn-nav,
.btn-nav-dark,
.active-btn-nav,
.active-btn-nav-dark {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-decoration: none;
	color: #333;
	font-size: 12px;
	font-weight: 500;
	cursor: pointer;
	width: 100%;
	height: 100%;
	justify-content: center;
}

.active-btn-nav svg,
.active-btn-nav-dark svg {
	fill: #ffc463;
	margin-bottom: 3px;
}

.btn-nav-dark,
.active-btn-nav-dark {
	color: #fff;
}

.btn-nav svg path,
.btn-nav-dark svg path,
.btn-nav svg circle,
.btn-nav-dark svg circle {
	fill: #333;
}

.btn-nav-dark svg path,
.btn-nav-dark svg circle {
	fill: #fff;
}

.btn-nav div,
.btn-nav-dark div {
	margin-top: 3px;
}

.metric-header {
	width: 200px;
	height: 25px;
	border-radius: 10px 10px 0 0;
	font-size: 16px;
	padding: 8px;
	color: #333;
}

.metric-body {
	width: 200px;
	height: 50px;
	border-radius: 0 0 10px 10px;
	padding: 6px;
	font-size: 30px;
}

.metric-body div {
	display: flex;
	align-items: baseline;
}

.metric {
	margin-right: 50px;
}

.metric-list {
	display: flex;
	margin: 20px 0;
	overflow-x: auto;
	max-width: 940px;
	mask-image: linear-gradient(to right, transparent, black 0, black 98%, transparent);
	-webkit-mask-image: linear-gradient(to right, transparent, black 0, black 98%, transparent);
}

.bling-strip-icon {
	height: 50px;
	border-radius: 8px;
	font-size: 18px;
	margin-right: 10px;
	color: #333;
}

.bling-strip-icon svg {
	margin-right: 10px;
}

/* MIGHT NOT BE NECESSARY */

.block,
.block2,
.block2-dark,
.dropdown,
.dropdown-dark {
	display: flex;
	align-items: center;
	justify-content: center;
}

.block2,
.block2-dark {
