}

.section,
.section-dark {
	display: inline-flex;
	height: 45px;
	margin-top: 10px;
	font-size: 15px;
	border: 2px solid #ececfa;
	cursor: pointer;
}

.section-dark {
	border: 2px solid #4b5188;
}

.subinfo-row,
.subinfo-row-dark {
	display: flex;
	height: 40px;
	font-size: 15px;
}

.subinfo-row:hover {
	background-color: #f8f8f8;
}

.subinfo-row-dark:hover {
	background-color: #3a3a3a;
}

.section-container,
.bling-strip {
	padding: 5px;
	overflow: auto;
	mask-image: linear-gradient(to right, transparent, black 7px, black 97%, transparent);
	-webkit-mask-image: linear-gradient(to right, transparent, black 7px, black 97%, transparent);
}

.section-container {
	max-width: 940px;
}

.bling-strip {
	display: flex;
}

.accor > .body,
.accor > .body-dark {
	margin-bottom: 15px;
	border: 2px solid transparent;
	border-top: 0px;
	max-height: 0;
	overflow: hidden;
	-webkit-transform: translate(0, 0);
	-moz-transform: translate(0, 0);
	transform: translate(0, 0);
	-webkit-transition: max-height 0.5s ease-in-out, border 0.5s ease-in-out;
	-moz-transition: max-height 0.5s ease-in-out, border 0.5s ease-in-out;
	transition: max-height 0.5s ease-in-out, border 0.5s ease-in-out;
}

.accor.active > .body,
.accor.active > .body-dark {
	margin-bottom: 15px;
	max-height: 600px;
	border: 2px solid #ececfa;
	border-top: 0px;
}

.accor.active > .body-dark {
	border: 2px solid #4b5188;
	border-top: 0px;
}

.dropdown,
.dropdown-dark {
	background-color: #6264bd;
	width: 45px;
	cursor: pointer;
}

.dropdown-dark {
	background-color: #363b63;
}

.dropdown.active svg {
	transform: rotate(180deg);
}

