/* PAGE TRANSITIONS: Credit, https://blog.etch.team/react-page-transitions-make-your-website-feel-native-bf2804b011dc */

.page,
.page-prev {
	width: 100%;
	position: fixed;
	-webkit-overflow-scrolling: touch;
	transition: transform 900ms ease-in-out, opacity 900ms ease-in-out;
	will-change: transform;
}

.page-enter {
	transform: translate3d(-100%, 0, 0);
}

.page-enter .page_contents {
	opacity: 0;
	transform: scale(0.87);
}

.page-enter-active {
	transform: translate3d(0, 0, 0);
}

.page-enter-active .page_contents {
	opacity: 1;
	transform: scale(1);
	transition: transform 900ms ease-in-out, opacity 900ms ease-in-out;
}

.page-exit {
	transform: translate3d(-100%, 0, 0);
}

.page-exit .page_contents {
	opacity: 1;
	transform: scale(1);
}

.page-exit-active .page_contents {
	opacity: 0;
	transform: scale(0.87);
	transition: transform 900ms ease-in-out, opacity 900ms ease-in-out;
}

.page-prev.page-enter {
	transform: translate3d(100%, 0, 0);
}

.page-prev.page-enter .page_contents {
	opacity: 0;
	transform: scale(0.87);
}

.page-prev.page-enter-active {
	transform: translate3d(0, 0, 0);
}

.page-prev.page-enter-active .page_contents {
	opacity: 1;
	transform: scale(1);
	transition: transform 900ms ease-in-out, opacity 900ms ease-in-out;
}

.page-prev.page-exit {
	transform: translate3d(100%, 0, 0);
}

.page-prev.page-exit .page_contents {
	opacity: 1;
	transform: scale(1);
}

.page-prev.page-exit-active .page_contents {
	opacity: 0;
	transform: scale(0.87);
	transition: transform 900ms ease-in-out, opacity 900ms ease-in-out;
}

/* Desktop */
@media screen and (min-width: 1201px) {
	.branding,
	.branding-dark {
		width: 28vw;
		font-size: 24px;
		height: 90px;
	}

	.branding svg,
	.branding-dark svg {
		transform: scale(1);
		margin: 0 5px;
	}

	.quarter,
	.quarter-dark {
		font-size: 16px;
		width: 110px;
	}

	.searchbox,
	.searchbox-dark {
		width: 28vw;
		padding-bottom: 10px;
	}

	.searchbox input,
	.searchbox-dark input {
		width: calc(28vw - 200px);
	}

	.list,
	.list-dark,
	.list-short,
	.list-short-dark {
		width: 28vw;
		height: calc(100vh - 190px);
	}

	.list-short,
	.list-short-dark {
		height: calc(100vh - 241px);
	}

	.card,
	.card-dark,
	.blankcard,
	.blankcard-dark,
	.card-active,
	.card-active-dark {
		margin: 0 5%;
	}

	.content,
	.content-dark {
		padding: 20px 50px;
		width: calc(72vw - 100px);
		overflow-y: scroll;
	}

	.metric {
		margin-right: 20px;
	}
}

/* Expanded */
@media screen and (min-width: 1000px) and (max-width: 1200px) {
	.searchbox input,
	.searchbox-dark input {
		width: calc(30vw - 120px);
	}

	.searchbox,
	.searchbox-dark {
		padding-bottom: 5px;
	}

	.branding,
	.branding-dark {
		width: 30vw;
		height: 80px;
		font-size: 20px;
	}

	.branding svg,
	.branding-dark svg {
		margin: 0 5px;
	}

	.list,
	.list-dark,
	.list-short,
	.list-short-dark {
		width: 30vw;
		padding: 10px 0;
		height: calc(100vh - 155px);
	}

	.list-short,
	.list-short-dark {
		height: calc(100vh - 205px);
	}

	.quarter,
	.quarter-dark {
		width: 50px;
		font-size: 16px;
		text-indent: 6px;
	}

	.card,
	.card-dark,
	.blankcard,
	.blankcard-dark,
	.card-active,
	.card-active-dark,
	.card::after,
	.card-dark::after {
		max-width: calc(28vw);
	}

	.blankcard-header,
	.blankcard-header-dark,
	.blankcard-subtitle,
	.blankcard-subtitle-dark {
		width: 80px;
		height: 22px;
		margin-bottom: 7px;
	}

	.blankcard-subtitle,
	.blankcard-subtitle-dark {
		width: 230px;
		height: 16px;
		margin-bottom: 0;
	}

